@import '../../assets/css/variable.less';

* {
  box-sizing: border-box;
}

.token-box {
  border-top: 1px solid @divider_color;

  a {
    text-decoration: none;
  }

  .token-info {
    width: 760px;
    border: 1px solid @divider_color;
    border-top: 0;
    margin: 0 auto;

    .token-header {
      display: flex;
      padding: 0 24px;
      justify-content: space-between;

      .token-header-left {
        width: 444px;

        .logo-title {
          height: 48px;
          display: flex;
          align-items: center;
          margin-top: 24px;

          .title-text {
            margin-left: 24px;
            font-size: 32px;
            color: #FFFFFF;
            font-weight: normal;
          }
        }

        .token-header-des {
          margin-top: 14px;
          font-weight: normal;
        }
      }

      .token-header-right {
        width: 242px;
        margin-top: 48px;
      }

      .learn-more {
        color: @button_outline_color;
        text-decoration: none;
      }
    }

    .token-info-content {
      width: 758px;
      display: flex;
      border-top: 1px solid @divider_color;
      border-bottom: 1px solid @divider_color;
      border-radius: 4px;
      margin-top: 73px;
      overflow: hidden;

      .top-info-box,
      .buttom-info-box {
        display: flex;
      }

      .token-burning {
        width: 50%;
        .info-box{
          width: 100%;
        }
      }

      .info-box {

        height: 136px;
        border-right: 1px solid @divider_color;
        background: rgba(85, 119, 253, 0.1);
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        .num {
          font-size: 24px;
          color: @whiteFontColor;
          height: 45px;
          display: flex;
          align-items: center;
          img{
            margin-left: 8px;
          }
        }

        .text {
          font-size: 12px;
        }
      }

      .info-box:nth-child(4) {
        border: 0;
      }
    }
    .token-burning-content{
      margin-top: 37px;
      .buttom-info-box {
        .info-box{
          border-right: 0;
        }
      }
    }

    .buy-deri-token,
    .benefits-title,
    .faq-title {
      text-align: center;
      font-size: 18px;
      color: @whiteFontColor;
      margin-top: 61px;
    }

    .buy-deri-token-swap {
      display: flex;
      margin-top: 37px;
      overflow: hidden;

      .top-box {
        display: flex;
        justify-content: space-between;
      }

      .swap-box {

        height: 100px;
        background: rgba(85, 119, 253, 0.1);
        border: 1px solid #203B60;
        box-sizing: border-box;
        border-radius: 4px;
        display: flex;
        align-items: center;
        cursor: pointer;
        justify-content: center;
        flex-direction: column;

        .swap-name {
          font-size: 16px;
          color: @whiteFontColor;
        }

        img {
          width: 24px;
          height: 24px;
        }

      }

      .swap-box:hover {
        border: 1px solid @button_outline_color;
      }
    }

    .benefits-faq-box {
      margin-bottom: 48px;

      .benefits-box {
        border-bottom: 1px solid @divider_color;
        margin-bottom: 48px;

        .benefits-info {
          width: 675px;
          height: 90px;
          margin: 0 auto;
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: 48px;
          margin-top: 24px;

          .governance,
          .privilege {
            width: 303px;
            padding: 0 16px;
            display: flex;
            justify-content: space-between;
            align-items: center;

            .governance-des,
            .privilege-des {
              margin-left: 16px;

              .governance-title,
              .privilege-title {
                font-weight: 600;
                font-size: 18px;
                color: #F3F4F8;
              }

              .governance-text,
              .privilege-text {
                font-weight: 400;
                font-size: 14px;
                margin-top: 16px;
              }
            }

          }
        }
      }

      .faq-box {
        .faq-info {
          margin-top: 35px;
          border: 1px solid @divider_color;
          border-bottom: 0;
          background: linear-gradient(106.26deg, rgba(97, 122, 255, 0.1) 1.22%, rgba(223, 229, 255, 0) 51.54%, rgba(85, 91, 237, 0.05) 100.82%);

          .faq-info-box {
            min-height: 48px;
            border-bottom: 1px solid @divider_color;

            .faq-title-img {
              display: flex;
              justify-content: space-between;
              align-items: center;
              height: 48px;
              padding-left: 48px;
              padding-right: 24px;
              cursor: pointer;
            }

            .faq-des {
              font-size: 14px;
              padding-left: 48px;
              padding-right: 24px;
              padding-bottom: 25px;
              font-weight: normal;

              .governance-privilege {
                color: #5577FD;

              }

              .privilege,
              .specifically,
              .specifically-des {
                margin-top: 16px;
              }

              .specifically-des {
                display: flex;

                .list-style {
                  background-color: #5577FD;
                  min-width: 10px;
                  height: 10px;
                  border-radius: 50%;
                  margin-top: 4px;
                  margin-right: 4px;
                }
              }
            }
          }
        }

      }
    }
  }
}

@media only screen and (width : 760px) {
  .token-box {
    .token-info {
      border: 0;
    }
  }
}

@media only screen and (max-width : 759px) {
  .token-box {
    .token-info {
      width: 100%;
      height: 100%;
      min-width: 375px;
      border: 0;

      .token-header {
        display: block;

        .token-header-left {
          width: 100%;
        }

        .token-header-right {
          margin-top: 42px;
        }
      }

      .token-info-content {
        width: 100%;
        margin-top: 24px;
        padding: 0 24px;
        display: block;
        border: 0;

        .top-info-box,
        .buttom-info-box {
          width: 100%;
          justify-content: space-between;

          .info-box {
            width: 50%;
          }

          .min-box {
            border-right: 0;
          }
        }
        .token-burning {
          .info-box{
            width: 100%;
            border-right: 0;
          }
        }

        .top-info-box {
          border-bottom: 1px solid @divider_color;
        }
      }

      .buy-deri-token-swap {
        width: 100%;
        flex-wrap: wrap;
        padding: 0 24px;

        a {
          flex: 1;
          margin: 0 16px 16px 0;
          width: calc((100% - 32px) / 2);
          min-width: calc((100% - 32px) / 2);
          max-width: calc((100% - 32px) / 2);
        }

        .swap-box {
          width: 100%;
        }
      }

      .benefits-faq-box {
        .benefits-box {
          .benefits-info {
            width: 100%;
            min-width: 375px;
            flex-wrap: wrap;
            height: 100%;

            .privilege {
              margin-top: 24px;
            }
          }
        }

        .faq-box {
          .faq-info {
            .faq-info-box {

              .faq-title-img,
              .faq-des {
                padding-left: 24px;
              }
            }
          }
        }
      }
    }
  }
}

@media only screen and (max-width : 375px) {
  width: 100%;
  flex-wrap: wrap;
  padding: 0 24px;

  a {
    flex: 1;
    margin: 0 16px 16px 0;
    width: 156px;
  }

  .swap-box {
    width: 100%;
  }
}

@media only screen and (min-width : 760px) {
  .token-box {
    .token-info {
      height: 100%;

      .token-header {
        height: 124px;
      }
    }

    .token-info-content {
      height: 136px;

      .info-box {
        width: 190px;
      }
    }

    .buy-deri-token-swap {
      width: 759px;
      justify-content: space-between;

      .swap-box {
        width: 184px;
      }
    }
  }
}